<template>
  <div class="container">
    <h1>The Super Quiz</h1>
    <hr />
    <br />
    <br />
    <transition mode="out-in">
      <component
        :is="selectedComponent"
        @changeComponent="changeComponent($event)"
        :userAnswer="userAnswer"
      >
        <h3>Your Score : {{correctAnswers}}/{{totalQuestions}}</h3>
      </component>
    </transition>
  </div>
</template>

<script>
import appQuestion from "./components/Question";
import appAnswer from "./components/Answer";
export default {
  data() {
    return {
      selectedComponent: "app-question",
      userAnswer: "",
      totalQuestions : 0,
      correctAnswers : 0
    };
  },
  components: {
    appQuestion,
    appAnswer
  },
  methods: {
    changeComponent(value) {
      this.userAnswer=value;
      this.selectedComponent == "app-question"
        ? (this.selectedComponent = "app-answer")
        : (this.selectedComponent = "app-question");
      if(value==true){
        this.correctAnswers++;
        this.totalQuestions++;
      }
      else if(value==false||value=="timeup")
        this.totalQuestions++;
    }
  }
};
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
.container {
  width: 80%;
  margin: auto;
}
h1 {
  text-align: center;
  font-weight: 600;
  font-size: 40px;
  padding: 20px 0px;
}
button {
  background-color: rgb(47, 93, 153);
  color: white;
  padding: 10px 20px;
  display: block;
  margin: auto;
  cursor: pointer;
  border-radius: 4px;
}
.v-leave-active{
  animation: rotateOut .5s linear;
}
.v-enter-active{
  animation: rotateIn .5s linear;
}
@keyframes rotateOut {
  from{
    transform: rotateY(0deg); 
  }
  to{
    transform: rotateY(90deg);
  }
}
@keyframes rotateIn {
  from{
    transform: rotateY(90deg); 
  }
  to{
    transform: rotateY(0deg);
  }
}
@media screen and (max-width:768px){
  .container{
    width: 95%;
  }
}
h3{
  text-align: center;
  padding: 20px 0px;
  letter-spacing: .1px;
  font-size: 24px;
}
</style>


