<template>
<section>
  <div v-backColor>
    <h1 v-highlight>{{renderAnswer}}</h1>
    <button @click="nextQuestion()">Next Question</button>
  </div>
  <slot></slot>
</section>
</template>

<script>
export default {
  props: ["userAnswer"],
  computed: {
    renderAnswer() {
      if (this.userAnswer == true) {
        return `That's Correct`;
      } else if (this.userAnswer == false) {
        return `That's Wrong`;
      } else {
        return "Time's Up";
      }
    }
  },
  directives: {
    backColor: {
      bind(el, binding, vnode) {
        vnode.context.userAnswer == true
          ? (el.style.backgroundColor = "lightgreen")
          : vnode.context.userAnswer == false
          ? (el.style.backgroundColor = "#ffb3b3")
          : (el.style.backgroundColor = "#ffb3b3");
      }
    },
    highlight: {
      bind(el, binding, vnode) {
        vnode.context.userAnswer == true
          ? (el.style.color = "darkgreen")
          : vnode.context.userAnswer == false
          ? (el.style.color = "#cc0000")
          : (el.style.color = "#cc0000");
      }
    }
  },
  methods: {
    nextQuestion() {
      this.$emit("changeComponent", "fhjdk");
    }
  }
};
</script>

<style scoped>
div {
  padding: 10px 0px;
  width: 50%;
  margin: auto;
}
h1 {
  background-color: inherit;
  text-align: center;
  padding: 15px 0px;
  border-bottom: 1px solid gray;
}
button {
  border: none;
  margin: 20px auto 10px;
}
@media screen and (max-width: 768px) {
  div {
    width: 100%;
  }
}
</style>


