<template>
  <div class="container1">
    <p class="question">{{generateQuestion}}</p>
    <div class="answer">
      <span class="timer">{{timer}}</span>
      <button v-for="(option,index) in options" :key="index" @click="checkAnswer">{{option}}</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num1: 0,
      num2: 0,
      operators: ["+", "-", "*", "%"],
      operator: "",
      answer: 0,
      options: [],
      userAnswer: "",
      timer: 10
    };
  },
  computed: {
    generateQuestion() {
      this.num1 = Math.ceil(Math.random() * 490 + 10);
      this.num2 = Math.ceil(Math.random() * 490 + 10);
      this.operator = this.operators[Math.floor(Math.random() * 4)];
      this.answer = eval(`${this.num1} ${this.operator} ${this.num2}`);
      let arr=[-2,-4,-7,-9,-10,-5,5,2,7,4,9,10];
      let pos = Math.floor(Math.random() * this.options.length);
      let arrel=arr.splice([Math.floor(Math.random()*arr.length)],1);
      this.options.splice(pos, 0, this.answer + arrel[0]);
      pos = Math.floor(Math.random() * this.options.length);
      arrel=arr.splice([Math.floor(Math.random()*arr.length)],1);
      this.options.splice(pos, 0, this.answer + arrel[0]);
      pos = Math.floor(Math.random() * this.options.length);
      this.options.splice(pos, 0, this.answer);
      arrel=arr.splice([Math.floor(Math.random()*arr.length)],1);
      pos = Math.floor(Math.random() * this.options.length);
      this.options.splice(pos, 0, this.answer - arrel[0]);
      return `What's ${this.num1} ${this.operator} ${this.num2} ?`;
    }
  },
  methods: {
    checkAnswer(e) {
      if (e.target.textContent == this.answer) this.userAnswer = true;
      else this.userAnswer = false;
      this.$emit("changeComponent", this.userAnswer);
    }
  },
  created() {
    setTimeout(() => {
      this.$emit("changeComponent", "timeup");
    }, 10000);
    const interval = setInterval(() => {
        this.timer--;
    }, 1000);
  }
};
</script>

<style scoped>
.container1 {
  border: 1px solid gray;
  border-radius: 3px;
  width: 50%;
  margin: auto;
}
.question {
  border-bottom: 1px solid gray;
  background-color: rgb(241, 241, 241);
  text-align: center;
  font-size: 18px;
  border-bottom: 1px solid gray;
  padding: 15px 0px;
  margin: 0px;
}
.answer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 25px;
  padding: 30px 0px;
  position: relative;
}
.timer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid gray;
  width: 40px;
  height: 40px;
  padding-top: 10px;
  text-align: center;
  border-radius: 50%;
}
@media screen and (max-width: 768px) {
  .container1 {
    width: 100%;
  }
}
</style>


